.section-header {
  background-color: $pink;
}
.container-section-header {
  box-sizing: border-box;
  width: 100%;
  max-width: 1280px;
  margin: 0 auto;
}

.header__container {
  width: 100vw;
  background-color: $pink;
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 6px;
  padding-bottom: 0;
  display: flex;
  align-items: center;
  align-content: space-between;

  position: relative;

  @media screen and (min-width: 768px) and (max-width: 1279px) {
    padding-top: 34px;
    padding-left: 34px;
    padding-right: 46px;
    position: relative;
  }
  @media screen and (min-width: 1280px) {
    padding-left: 46px;
    padding-right: 115px;
    padding-top: 34px;
    justify-content: space-between;
  }
}
.header__button {
  width: 159px;
  height: 44px;
  background-color: rgba(255, 255, 255, 1);
  border-radius: 22px;
  color: rgba(212, 20, 67, 1);
  font-weight: 700;
  font-size: 16px;
  line-height: 1.301;
  border-style: none;
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  padding-left: 10px;
  padding-top: 2px;
  // margin-left: 110px;

  &:active,
  &:hover,
  &:focus {
    background-color: $link-hover;
  }

  @media screen and (max-width: 767px) {
    display: none;
  }
  @media screen and (min-width: 768px) {
    position: absolute;
    top: 38px;
    right: 34px;
    z-index: 1;
  }
  @media screen and (min-width: 1280px) {
    position: static;
  }
}
.header__navigation-list {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  column-gap: 35px;
  text-align: center;
  list-style: none;

  @media screen and (max-width: 1279px) {
    display: none;
  }
}
.header__navigation-list-item {
  color: rgba(255, 255, 255, 1);
  font-weight: 700;
  font-size: 16px;
  line-height: 1.301;
  cursor: pointer;

  text-decoration: none;
  &:active,
  &:hover,
  &:focus {
    color: $dark-pink;
  }
}

.header__nav-container {
  margin: left 50px;
  z-index: 2;
}
.header__icon-mobile-menu {
  stroke: $dark-pink;
}

.header__logo-container {
  width: 199px;
  height: 50px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;

  position: relative;
  @media screen and (min-width: 1280px) {
    margin-right: 110px;
  }
}
.header__button-mobile-menu {
  background-color: transparent;
  border: none;
  position: absolute;
  top: 15px;
  right: 20px;
  cursor: pointer;

  @media screen and (min-width: 768px) {
    position: absolute;
    top: 52px;
    right: 224px;
    z-index: 1;
  }
  @media screen and (min-width: 1280px) {
    display: none;
  }
}
.header__logo-text {
  font-family: $font;
  font-weight: 400;
  font-size: 22px;
  line-height: 1.145;
  color: $dark-pink;
  text-transform: uppercase;
}
.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}
.menu-mobile {
  width: 250px;
  height: 100vh;
  position: fixed;
  z-index: 200;
  background-color: $modal-pink;
  margin: auto;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  margin-left: auto;
  margin-right: auto;
  padding-bottom: 171px;
  // padding-top: 60px;

  @media screen and (min-width: 768px) and (max-width: 1280px) {
    width: 254px;
    height: 1020px;
    // transform:translateX(40%);
  }
}

.menu-mobile__list-link {
  color: $white;
  font-weight: 700;
  font-size: 14px;
  line-height: 1.302;
  text-decoration: none;

  &:active,
  &:hover,
  &:focus {
    color: $dark-pink;
  }
}
.menu-mobile__list-item {
  height: 42px;
  width: 250px;
  border: 1px solid #e18298;
  padding-left: 24px;
  list-style: none;
}
.menu-mobile__list-item:nth-child(1) {
  padding-bottom: 11px;
  padding-top: 13px;
  margin-top: 19px;
}
.menu-mobile__list-item:nth-child(2) {
  padding-bottom: 12px;
  padding-top: 11px;
}
.menu-mobile__list-item:nth-child(3) {
  padding-bottom: 13px;
  padding-top: 11px;
}
.menu-mobile__list-item:nth-child(4) {
  padding-bottom: 13px;
  padding-top: 10px;
}
.menu-mobile__list-item:nth-child(5) {
  padding-bottom: 14px;
  padding-top: 10px;
}
.header__button-mobile {
  width: 209px;
  height: 44px;
  background-color: rgba(255, 255, 255, 1);
  border-radius: 22px;
  color: rgba(212, 20, 67, 1);
  font-weight: 700;
  font-size: 16px;
  line-height: 1.301;
  border-style: none;
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  cursor: pointer;

  &:active,
  &:hover,
  &:focus {
    background-color: $link-hover;
  }
}
.menu-mobile__button-container {
  padding-left: 21px;
  padding-top: 60px;
}

.menu-mobile__button-close-container {
  width: 100%;
  height: 14px;
  position: relative;
}
.menu-mobile__button-close {
  border: none;
  background-color: transparent;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: auto;
  position: relative;
}
.menu-mobile__icon {
  stroke: $white;
  position: absolute;
  top: 23px;
  right: 28px;
}

.header__button-mobile-icon {
  fill: $dark-pink;
  margin-left: 12px;
}
.mob-menu__list-block {
  padding-left: 0;
}
